import './Footer.css';

const Footer = ({items, completed, active }) => {
    let result = items.length;
    let one = completed.length;
    let two = active.length;
    console.log(two);
   
    return ( 
        <div className="footer">
            <p>{result} items left</p>
            <span className="opt" onClick={active}>{two}Active</span><br />
            <span className="opt" onClick={completed}>{one}Completed</span>
        </div>
     );
}
 
export default Footer;